@use '../util' as *;

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: rem(10);
  line-height: 1.1;
}

h1 {
  font-weight: 600;
}

h2 {
  font-weight: 600;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  font-weight: 400;
  line-height: 1.5;
  font-size: rem(15);
  color: var(--grayishblue);
}