.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3.75rem 1.25rem 3.75rem 1.25rem;
}
.header__title1 {
  font-size: 1.5rem;
  font-weight: 200;
}
.header__title2 {
  font-size: 1.5rem;
  margin-top: 0;
}
.header__p {
  margin-top: 0.625rem;
  min-width: 18.75rem;
  max-width: 28.125rem;
}

.card {
  display: grid;
  margin-bottom: 1.875rem;
  padding: 1.875rem;
  height: 15.625rem;
  background: var(--white);
  border-radius: 0.25rem;
  box-shadow: 0.3125rem 0.75rem 0.75rem rgba(0, 0, 0, 0.1);
}
.card__title {
  margin-bottom: -0.3125rem;
}
.card__img {
  justify-self: end;
}
.card--a {
  border-top: 0.3125rem solid var(--cyan);
}
.card--b {
  border-top: 0.3125rem solid var(--red);
}
.card--c {
  border-top: 0.3125rem solid var(--orange);
}
.card--d {
  border-top: 0.3125rem solid var(--blue);
}
@media (min-width: 48em) {
  .card--a {
    grid-column: 1;
    grid-row: 1/3;
    align-self: center;
  }
  .card--b {
    grid-column: 2/3;
    grid-row: 1;
  }
  .card--c {
    grid-column: 2/3;
    grid-row: 2;
  }
  .card--d {
    grid-column: 3/4;
    grid-row: 1/3;
    align-self: center;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  font-family: "Poppins", sans-serif;
  background: var(--veryltgray);
  height: 100vh;
  padding: 1.25rem 0.625rem 0 0.625rem;
}
body footer {
  font-size: 5.625rem;
  margin-top: 3.75rem;
  margin-bottom: 0.625rem;
}
@media (min-width: 48em) {
  body main {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1.25rem;
    width: auto;
    max-width: 75rem;
  }
}

:root {
  --red: hsl(0, 78%, 62%);
  --cyan: hsl(180, 62%, 55%);
  --orange: hsl(34, 97%, 64%);
  --blue: hsl(212, 86%, 64%);
  --verydkblue: hsl(234, 12%, 34%);
  --grayishblue: hsl(229, 6%, 66%);
  --veryltgray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0.625rem;
  line-height: 1.1;
}

h1 {
  font-weight: 600;
}

h2 {
  font-weight: 600;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  font-weight: 400;
  line-height: 1.5;
  font-size: 0.9375rem;
  color: var(--grayishblue);
}